@import 'ScssHelpers/helpers-all.scss';

.event_list {
	background-color: $white_2;
}

.event_list__head {
	max-width: 52rem;
	margin: 0 auto;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}
}

.event_list__subtitle {
	@include subtitle;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.event_list__title {
	@include h2;

	font-family: $font_2;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.event_list__text {
	@include text;

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.event_list__text_result {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		font-style: italic;
		font-weight: 500;
		font-size: 1.2rem;
		line-height: 140%;
		color: $black_2;
	}
}

.event_list__wrap {
	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}
}

.event_list__items {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_4, min) {
		margin: 0 -2rem -2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1.6rem -2rem 0;
	}
}

.event_list__item {
	flex-shrink: 0;

	@include bp($point_4, min) {
		padding: 0 2rem 2rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.6rem 2rem 0;
	}

	&:nth-child(6n + 1),
	&:nth-child(6n + 2),
	&:nth-child(6n + 5),
	&:nth-child(6n + 6) {
		@include bp($point_1, min) {
			width: 25%;
		}

		@include bp($point_1 - 1) {
			width: 50%;

			&.event_list__item--width_mod {
				width: 100%;
			}
		}
	}

	&:nth-child(6n + 3),
	&:nth-child(6n + 4) {
		width: 50%;

		@include bp($point_1 - 1) {
			&.event_list__item--width_mod {
				width: 100%;
			}
		}
	}
}

.event_list__link_mob {
	@include bp($point_2, min) {
		display: none;
	}
	@include bp($point_3) {
		text-align: center;
	}
}
