@import 'ScssHelpers/helpers-all.scss';

.event_card {
	position: relative;
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-start;

	border-radius: $gl_radius;
	overflow: hidden;

	transition: box-shadow 0.3s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		box-shadow: (0px 0px 3rem rgba(20, 20, 20, 0.7));
	}

	@include bp($point_2, min) {
		min-height: 60rem;
		padding: 4.3rem 2rem 4.3rem 3.5rem;
	}

	@include bp($point_3) {
		min-height: 28rem;
		padding: 2.2rem 2rem;

		.venue_spaces & {
			padding: 0;
		}

		.venue_spaces--slider_mod & {
			min-height: 34rem;
		}
	}
}

.event_card__pic {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.event_card__img {
	@include cover_img;

	transition: transform 0.3s ease;

	@media (any-hover: hover) {
		.event_card:hover & {
			transform: scale(1.1);
		}
	}
}

.event_card__content {
	position: relative;
	z-index: 2;
	text-align: left;

	@include bp($point_2, min) {
	}

	@include bp($point_3) {
		.venue_spaces & {
			width: 100%;
			background-color: $black_2;
			padding: 1.6rem 2.4rem 2rem;
		}
	}
}

.event_card__title {
	color: $white_2;

	@include bp($point_1, $point_2) {
		word-break: break-all;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4, min) {
		@include h3;
		font-family: $font_2;
	}

	@include bp($point_4 - 1) {
		font-weight: 700;
		font-size: 1.8rem;
		line-height: 110%;
	}
}

.event_card__descr {
	display: flex;
	align-items: center;
	@include text;

	color: $white_2;

	@include bp($point_3) {
		&:not(.venue_spaces &) {
			display: none;
		}
	}
}

.event_card__icon {
	width: 1.4rem;
	height: 1.5rem;

	color: $white_2;

	&:not(:last-child) {
		margin-right: 1rem;
	}

	@include bp($point_3) {
		.venue_spaces--slider_mod & {
			display: none;
		}
	}
}
