@import 'ScssHelpers/helpers-all.scss';

.contact__in {
	max-width: 106rem;
}

.contact__title {
	@include h2;

	max-width: 78rem;
	width: 100%;
	margin-left: auto;
	margin-right: auto;

	font-family: $font_2;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}
		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.contact__descr {
	max-width: 76rem;
	width: 100%;
	margin: 0 auto;

	font-weight: 500;
	letter-spacing: 0.03em;

	text-align: center;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 130%;
	}
	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 140%;
	}
}

.contact__heading {
	position: relative;

	&:after {
		position: absolute;

		left: 50%;
		display: block;

		height: 1px;
		content: '';

		background-color: $black_2;
		transform: translateX(-50%);

		@include bp($point_2, min) {
			bottom: -5rem;
			width: 12rem;
		}
		@include bp($point_3) {
			bottom: -2.2rem;
			width: 6rem;
		}
	}

	@include bp($point_2, min) {
		margin-bottom: 10.4rem;
	}
	@include bp($point_3) {
		margin-bottom: 4rem;
	}
}

// **************************
// ***********form***********
// **************************

.contact__form_row_w {
	@include bp($point_2, min) {
		margin-bottom: 3.4rem;
	}
	@include bp($point_3) {
		margin-bottom: 2rem;
	}

	&.contact__form_row--offset_mod {
		@include bp($point_2, min) {
			margin-bottom: 4.6rem;
		}
	}
}

.contact__form_row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);

		margin: 0 -2.6rem -2.6rem 0;

		.contact__form_row--col_size_mod & {
			margin: 0 -4.5rem -2.6rem 0;
		}
	}

	@include bp($point_3) {
		margin-bottom: -2rem;
	}
}

.contact__form_col {
	.contact__form_row--col_size_mod & {
		@include bp($point_2, min) {
			padding: 0 4.5rem 2.6rem 0;

			&:nth-child(2n + 1) {
				width: 61.5%;
			}

			&:nth-child(2n + 2) {
				width: 38.5%;
			}
		}
	}

	.contact__form_row--col_textarea_mod & {
		@include bp($point_2, min) {
			width: 100%;
		}
	}

	// &:not(.contact__form_row--col_textarea_mod &, .contact__form_row--col_size_mod &) {
	// 	@include bp($point_2, min) {
	// 		padding: 0 2.6rem 2.6rem 0;
	// 		width: 50%;
	// 	}
	// }

	@include bp($point_2, min) {
		padding: 0 2.6rem 2.6rem 0;
		width: 50%;
	}

	@include bp($point_3) {
		padding-bottom: 2rem;
		width: 100%;
	}
}

// SELECT

.contact__form_select_w {
	position: relative;

	&:after {
		position: absolute;

		z-index: 1;

		content: '\25BC';
		font-size: 2rem;
		color: $black_2;
		pointer-events: none;
		transition: transform ease 0.3s;

		@include bp($point_2, min) {
			top: 2rem;
			right: 2.8rem;
		}
		@include bp($point_3) {
			top: 2.4rem;
			right: 1.8rem;
		}
	}

	// &:hover::after {
	// 	transform: rotate(180deg);
	// }

	@include bp($point_3) {
		padding-top: 1rem;
		width: 100%;
		margin: 0 auto;
	}
}

.contact__form_select {
	width: 100%;
	color: black;

	border: 2px solid $black_2;

	background-color: $white_2;
	border-radius: 0.8rem;

	appearance: none;
	-webkit-appearance: none;
	overflow: hidden;

	font-weight: 700;
	font-family: $font_2;

	option {
		font-size: 1.8rem;
		line-height: 1;
		font-weight: 400;
		font-family: $font_1;
	}

	cursor: pointer;
	transition: background-color ease 0.3s;

	// &:hover {
	// 	background-color: $white;
	// }

	@include bp($point_2, min) {
		height: 6.4rem;
		padding: 0 5rem 0 2.2rem;
		font-size: 2.8rem;
		line-height: 6.4rem;
	}

	@include bp($point_3) {
		height: 5.4rem;
		padding: 0 5rem 0 2rem;
		width: 100%;
		font-size: 2rem;
		line-height: 5.4rem;
	}
}
